export const query = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    homepageConfig: markdownRemark(
      fileAbsolutePath: { regex: "/homepage-config/" }
    ) {
      frontmatter {
        videoUrl
      }
    }
    posts: allMarkdownRemark(
      filter: { fileAbsolutePath: { regex: "/posts/" } }
      sort: { fields: [frontmatter___date], order: DESC }
      limit: 6
    ) {
      edges {
        node {
          fields {
            slug
          }
          id
          html
          frontmatter {
            title
            date(formatString: "YYYY-MM-DD")
            path
            thumbnail {
              childImageSharp {
                fluid {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
      }
    }
    allFile(
      limit: 10
      sort: { order: DESC, fields: absolutePath }
      filter: {
        extension: { regex: "/(jpg)|(png)|(tif)|(tiff)|(webp)|(jpeg)/" }
        absolutePath: { regex: "/screenshots/" }
      }
    ) {
      edges {
        node {
          absolutePath
          childImageSharp {
            fluid {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`;
